import React from 'react'

const cardData = [
  { id: 1, count: "N/A", heading: "Leads" },
  { id: 2, count: "N/A", heading: "Clients" },
  { id: 3, count: "N/A", heading: "Vendors" }
]

const Counter = () => {
  return (
    <div className='flex flex-col gap-5'>
      {
        cardData.length > 0 ? (
          cardData.map(ele => (
            <div key={ele.id} className=' bg-white relative rounded-[20px] w-full h-[250px] sm:h-[304px] flex-col flex justify-center items-center'>
              <h1 className='text-[65px] font-bold leading-[1.2]'>{ele.count || "--"}</h1>
              <h1 className='text-xl font-bold'>{ele.heading || "--"}</h1>
              <a href="" className=' text-xs font-bold absolute bottom-6 right-6  underline'>View all</a>
            </div>
          ))
        ) : <p className=' text-red-600 font-semibold text-2xl flex justify-center items-center h-full'>No data Found</p>
      }

    </div>
  )
}

export default Counter
