import React from "react";

const TableComponent = ({ title, name, role, lead, sno, status, showButton }) => {
  return (
    <div className="border border-border-dark rounded-[20px] overflow-hidden">
      <div className="flex bg-white  items-center py-1 px-4  justify-center relative">
        
        <h1 className="font-bold  p-2 text-center leading-8 text-2xl">
          {title}
        </h1>
     
          {
            showButton && (
              <button className=" absolute right-4 top-3">
            <img src={"/assets/svg/plusbutton.svg"} alt="Add" />
          </button>
            )
          }
      </div>
      <div className="w-full overflow-x-auto border-t border-border-dark min-h-[300px]">
        <table className="w-full border-collapse">
          <thead>
            <tr className="bg-[#F6F6F6] h-[41.9px]">
              <th className="text-base font-bold px-4 border-r border-border-dark w-[20%]">
                S no.
              </th>
              <th className="text-base font-bold px-4 border-r border-border-dark w-[30%]">
                Name
              </th>
              <th className="text-base font-bold px-4 border-r border-border-dark w-[20%]">
                Role
              </th>
              <th className="text-base font-bold px-4 border-r border-border-dark w-[20%]">
                Lead
              </th>
              <th className="text-base font-bold px-4 border-r border-border-dark w-[20%]">
                Status
              </th>
              <th className="text-base font-bold px-4 w-[20%]">
                Action
              </th>
            </tr>
          </thead>
          <tbody className="bg-white w-full h-[400px]">
            <tr>
              <td className="text-base font-bold px-4 border-r border-border-dark">{sno}</td>
              <td className="text-base font-bold px-4 border-r border-border-dark">{name}</td>
              <td className="text-base font-bold px-4 border-r border-border-dark">{role}</td>
              <td className="text-base font-bold px-4 border-r border-border-dark">{lead}</td>
              <td className="text-base font-bold px-4 border-r border-border-dark">{status}</td>
              <td className="text-base font-bold px-4"></td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
  );
};

export default TableComponent;
