import React from "react";
import TableComponent from "../../components/table/TableComponent";
import Counter from "../../components/counter/Counter";
const statusOptions = [
  { value: "active", label: "Active" },
  { value: "inactive", label: "Inactive" },
  { value: "pending", label: "Pending" },
];

const DashBoard = () => {
  return (
    <div className="">
      <div className="flex gap-5 max-md:flex-col-reverse">
        <div className="flex-[9]">
          <div className="h-[295px] w-full relative rounded-[20px] overflow-hidden">
            <img
              src="/assets/image/home/crm.png"
              alt=""
              className=" w-full  h-full object-cover"
            />
            <h5 className=" absolute left-20 top-24 leading-10 text-white text-[30px] font-bold">
              Dear Admin, <br />
              Welcome to CRM
            </h5>
          </div>
          <div className=" mt-7">
            <TableComponent title={"Employees"} statusOptions={statusOptions} status={true} />
          </div>
        </div>
        <div className="flex-[3]">
          <Counter/>

        </div>
      </div>
    </div>
  );
};

export default DashBoard;
