import React, { useState, useEffect } from 'react';
import ApiService from '../../apiService';
import { toast } from 'react-toastify';
import { useLocation, useNavigate, useParams } from 'react-router-dom';
import { getUserRoleFromToken } from "../../utils/auth";

const LeadForm = ({ isEditMode }) => {
    const { id: leadId , campId} = useParams();

    const location = useLocation();
    const query = new URLSearchParams(location.search);
    const campaignId = query.get("campaignId");

    const [loading, setLoading] = useState(false);
    const [camp, setCamp] = useState([]);
    const [questions, setQuestions] = useState([]);
    const [responses, setResponses] = useState({});
    const [uploadedDoc, setUploadedDoc] = useState(null);
    const [uploadedRecording, setUploadedRecording] = useState(null);
    const navigate = useNavigate();

    const userType = getUserRoleFromToken();

    useEffect(() => {
        if (campaignId && camp.length > 0) {
            const matchedCampaign = camp.find((d) => d.value === campaignId);
            if (matchedCampaign) {
                // setSelectedCamp(matchedCampaign.value);
                fetchQuestions(matchedCampaign.value);
            }
        }
    }, [camp, campaignId]);

    useEffect(() => {
        if (isEditMode && leadId) {
            fetchLeadData(leadId);
        }
    }, [leadId, isEditMode]);

    useEffect(() => {
        fetchCamps();
    }, []);

    useEffect(() => {
        if (campId || campaignId) {
            fetchQuestions(campId || campaignId);
        }
    }, [camp, campId, campaignId]);

    const fetchQuestions = async (campId) => {
        setLoading(true);
        try {
            const result = await ApiService.question.getQuestionsByCampId(campId);
            if (result?.success) {
                setQuestions(result.data || []);
            }
        } catch (error) {
            toast.error('Failed to fetch questions.');
        } finally {
            setLoading(false);
        }
    };

    const fetchLeadData = async (id) => {
        setLoading(true);
        try {
            const result = await ApiService.leads.getLeadById(id, 'vendor');
            if (result?.success) {
                const { data } = result;
                setResponses(
                    data.responses.reduce((acc, response) => {
                        acc[response.questionId] = response.response;
                        return acc;
                    }, {})
                );
            }
        } catch (error) {
            toast.error('Failed to fetch lead data.');
        } finally {
            setLoading(false);
        }
    };

    const fetchCamps = async () => {
        try {
            const data = await ApiService.campaigns.getById();
            if (data?.success) {
                setCamp(data.data.map((camp) => ({ label: camp.title, value: camp._id })));
            }
        } catch (error) {
            toast.error('Failed to fetch campaigns.');
        }
    };

    const handleInputChange = (questionId, value) => {
        setResponses((prevResponses) => ({
            ...prevResponses,
            [questionId]: value,
        }));
    };

    const handleFileChange = (event, type) => {
        const file = event.target.files[0];
        if (type === 'doc') {
            setUploadedDoc(file);
        } else if (type === 'recording') {
            setUploadedRecording(file);
        }
    };

    const handleSubmit = async (e) => {
        e.preventDefault();
        setLoading(true);

        // Check if files are uploaded for vendors
        if (!isEditMode && userType === 'vendor' && (!uploadedDoc || !uploadedRecording)) {
            toast.error('Both Document and Recording are required for vendors.');
            setLoading(false);
            return;
        }

        const formData = new FormData();
        formData.append("campaignId", campId || campaignId);

        Object.keys(responses).forEach((questionId) => {
            formData.append("responses[]", JSON.stringify({
                questionId,
                response: responses[questionId],
            }));
        });

        if (uploadedDoc) formData.append("media", uploadedDoc);  // Single 'media' field for both files
        if (uploadedRecording) formData.append("media", uploadedRecording);

        try {
            let response;
            if (isEditMode) {
                response = await ApiService.leads.updateLead(leadId, formData);
            } else {
                response = await ApiService.leads.createLead(formData);
            }

            if (response?.success) {
                toast.success(isEditMode ? 'Lead updated successfully!' : 'Lead created successfully!');
                navigate('/crm/leads');
            }
        } catch (error) {
            toast.error('Failed to save lead.');
        } finally {
            setLoading(false);
        }
    };

    return (
        <div className="lead-form-container bg-white pb-10 border-border-color border rounded-md overflow-hidden">
            <h1 className='text-xl font-bold text-center py-3 border-border-color border-b'>
                {isEditMode ? 'Edit Lead' : 'Create New Lead'}
            </h1>
            {/* <div className='p-4 flex items-center mt-5'>
                <label className="font-semibold text-base leading-5 flex-[4]">Select Campaign</label>
                <div className='flex-[8]'>
                    <Dropdown
                        options={[{ label: "Select Campaign", value: "" }, ...camp]}
                        onChange={(e) => setSelectedCamp(e.value)}
                        value={selectedCamp}
                        placeholder="Select a camp"
                        className="font-medium z-50"
                        disabled={!!campaignId}
                    />
                </div>
            </div> */}
            <form onSubmit={handleSubmit} className='p-3'>
                <div className="flex items-center gap-4 flex-wrap justify-center">
                    {questions?.map((question) => (
                        <div key={question.questionId} className="form-group mt-5 w-full md:w-[45%]">
                            <label className='text-lg font-medium'>
                                {question.title} {question.isRequired && <span className="text-red-500">*</span>}
                            </label>
                            {question.type === 'text' && (
                                <input
                                    type="text"
                                    value={responses[question.questionId] || ''}
                                    onChange={(e) => handleInputChange(question.questionId, e.target.value)}
                                    required={question.isRequired}
                                    className='outline-none w-full mt-2 border border-[#C4C4C4] bg-[#F9F9F9] p-2 rounded-md font-medium text-sm'
                                />
                            )}
                            {question.type === 'radio' && (
                                <div className="control mt-1 flex gap-3 items-center">
                                    <label className="radio flex gap-1 items-center">
                                        <input
                                            type="radio"
                                            name={`question_${question.questionId}`}
                                            checked={responses[question.questionId] === 'yes'}
                                            onChange={() => handleInputChange(question.questionId, 'yes')}
                                        />
                                        Yes
                                    </label>
                                    <label className="radio flex gap-1 items-center">
                                        <input
                                            type="radio"
                                            name={`question_${question.questionId}`}
                                            checked={responses[question.questionId] === 'no'}
                                            onChange={() => handleInputChange(question.questionId, 'no')}
                                        />
                                        No
                                    </label>
                                </div>
                            )}
                            {question.type === 'number' && (
                                <input
                                    type="number"
                                    value={responses[question.questionId] || ''}
                                    onChange={(e) => handleInputChange(question.questionId, e.target.value)}
                                    required={question.isRequired}
                                    className='outline-none w-full mt-2 border border-[#C4C4C4] bg-[#F9F9F9] p-2 rounded-md font-medium text-sm'
                                />
                            )}
                            {question.type === 'date' && (
                                <input
                                    type="date"
                                    value={responses[question.questionId] || ''}
                                    onChange={(e) => handleInputChange(question.questionId, e.target.value)}
                                    required={question.isRequired} 
                                    className='outline-none w-full mt-2 border border-[#C4C4C4] bg-[#F9F9F9] p-2 rounded-md font-medium text-sm'
                                />
                            )}
                            {question.type === 'email' && (
                                <input
                                    type="email"
                                    value={responses[question.questionId] || ''}
                                    onChange={(e) => handleInputChange(question.questionId, e.target.value)}
                                    required={question.isRequired}
                                    className='outline-none w-full mt-2 border border-[#C4C4C4] bg-[#F9F9F9] p-2 rounded-md font-medium text-sm'
                                />
                            )}
                        </div>
                    ))}
                </div>

                {questions.length > 0 && (
                    <div className="flex items-center gap-4 flex-wrap justify-center">
                        <div className="form-group mt-5 w-full md:w-[45%] flex-col flex">
                            <label className="text-lg font-medium">Upload Docs {userType === 'vendor' && <span className='text-red-600'>*</span>}</label>
                            <input
                                type="file"
                                onChange={(e) => handleFileChange(e, 'doc')}
                                className="mt-2 border border-[#C4C4C4] p-2 rounded-md"
                            />
                        </div>

                        <div className="form-group mt-5 w-full md:w-[45%] flex-col flex">
                            <label className="text-lg font-medium">Upload Recording  {userType === 'vendor' && <span className='text-red-600'>*</span>}</label>
                            <input
                                type="file"
                                onChange={(e) => handleFileChange(e, 'recording')}
                                accept="audio/*, video/*"
                                className="mt-2 border border-[#C4C4C4] p-2 rounded-md"
                            />
                        </div>
                    </div>
                )}

                <div className='flex mt-6 justify-center'>
                    <button
                        type="submit"
                        className={`py-3 px-8 rounded-[15px] bg-black text-white text-sm font-semibold ${loading || (userType === 'vendor' && (!uploadedDoc || !uploadedRecording)) ? "opacity-50 cursor-not-allowed" : ""}}`}
                        disabled={loading || (!isEditMode && userType === 'vendor' && (!uploadedDoc || !uploadedRecording))}
                    >
                        {loading ? <div className="loader" /> : (isEditMode ? 'Update Lead' : 'Create Lead')}
                    </button>
                </div>
            </form>
        </div>
    );
};

export default LeadForm;
